<template>
  <div>
    <el-form :model="form" :rules="rules" ref="ruleForm">
      <el-form-item label="" :label-width="formLabelWidth" prop="username">
        <el-input v-model="form.username" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="content">
        <el-input v-model="form.content" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="contact">
        <el-input v-model="form.contact" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="address">
        <v-distpicker v-model="form.address" @selected="selected"></v-distpicker>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="addressDetaile">
        <el-input v-model="form.addressDetaile" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
    </div>
  </div>
</template>

<script>
import VDistpicker from 'v-distpicker'

export default {
  name: "PerDonDialog",
  components: {VDistpicker},
  inject: ['reload'], // 
  data() {
    return {
      formLabelWidth: "120px",
      form: {},
      rules: {
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 18, message: ' 3  18 ', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '', trigger: 'blur'}
        ],
        contact: [
          {required: true, message: '', trigger: 'blur'}
        ],
        address: [
          {required: true, message: '', trigger: 'blur'}
        ],
        addressDetaile: [
          {required: true, message: '', trigger: 'blur'}
        ],
      }
    }
  },
  methods: {
    selected(item) {
      this.form.address = item.province.value + item.city.value + item.area.value
      console.log(item)
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$emit("releaseDialog", this.form)
        } else {
          return false;
        }
      });
    },
  }
}
</script>

<style scoped>
.dialog-footer {
  text-align: right;
}
</style>