<template>
  <div class="box-data">
    <el-row>
      <el-col :span="4">
        <h2></h2>
      </el-col>
      <el-col :span="4" :offset="16">
        <span>{{ this.getDay(0, 3600000) }} 18:40</span>
      </el-col>
    </el-row>
    <el-row :gutter="40">
      <data-card :data-card="cardData" />
    </el-row>
    <el-row>
      <span>:;;</span>
    </el-row>
    <ve-map :data="chartData"
            :colors="colors"
            class="map"
            :settings="chartSettings"
            height='1100px' width='100%'
            style=''
            :legend-visible='false'></ve-map>
  </div>
</template>

<script>
import DataCard from "@/views/data/child/DataCard";
import {mapState} from "vuex";
import donate from "@/assets/img/data/donate.svg";
import demand from "@/assets/img/data/demand.svg";

export default {
  name: "Data",
  components: {DataCard},
  computed: {
    ...mapState(["contractDonateFactory", "contractDemandFactory", "account"])
  },
  data() {
    this.chartSettings = {
      // position: 'province/henan', //  china   province
      labelMap: {
        'people': ''  //
      },
      itemStyle: {
        normal: {   //
          borderColor: '#fff',
          areaColor: "#E6EFFF",  //
        },
        emphasis: {  //
          areaColor: "#7F95D1",
        }
      },
      label: {    //
        show: true,
        fontSize: 13,
        fontWeight: 300,
        color: '#9498AC'
      },
    }
    this.colors = [
      "#7f95d1",
      "#e299b5",
    ]
    return {
      chartData: {
        columns: ['', '', ''],
        rows: [
          {'': '', '': 123, '': 123},
          {'': '', '': 1223, '': 2123},
          {'': '', '': 2123, '': 1243},
          {'': '', '': 2, '': 15},
          {'': '', '': 1626, '': 2452},
          {'': '', '': 2352, '': 25},
          {'': '', '': 1878, '': 606},
          {'': '', '': 4834, '': 134},
          {'': '', '': 272, '': 357},
          {'': '', '': 135, '': 603},
          {'': '', '': 3573, '': 25},
          {'': '', '': 135, '': 5056},
          {'': '', '': 5798, '': 1361},
          {'': '', '': 1362, '': 353},
          {'': '', '': 162, '': 3583},
          {'': '', '': 48, '': 468},
          {'': '', '': 1351, '': 1314},
          {'': '', '': 3473, '': 135},
          {'': '', '': 4984, '': 135},
        ]
      },
      cardData: [
        {
          style: 'blue',
          src: donate,
          title: '',
          num: 0,
          change: 0,
          fuhao: '+'
        },
        {
          style: 'pink',
          src: demand,
          title: '',
          num: 0,
          change: 0,
          fuhao: '-'
        },
      ]
    };
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.contractDemandFactory.index().then(res => {
        console.log(res)
        this.cardData[1].num = res[0]
      })
      this.contractDonateFactory.index().then(res => {
        console.log(res)
        this.cardData[0].num = res[0]
      })
    },
    getDay (day, hours) {
      var today = new Date()
      var targetday = today.getTime() + 1000 * 60 * 60 * 24 * day + hours
      today.setTime(targetday)
      var tYear = today.getFullYear()
      var tMonth = today.getMonth()
      var tDate = today.getDate() - 1
      // var getHours = today.getHours()
      tMonth = this.doHandleMonth(tMonth + 1)
      tDate = this.doHandleMonth(tDate)
      return tYear + '-' + tMonth + '-' + tDate
    },
    doHandleMonth (month) {
      var m = month
      if (month.toString().length === 1) {
        m = '0' + month
      }
      return m
    },
  }
}
</script>

<style scoped>
.box-data {
  width: 96%;
  margin: 0 auto;
}
span {
  line-height: 67px;
  color: dimgray;
}

.map {
  margin-top:-100px;
  /*z-index: -1;*/
}
h2 {
  color: #1e2947;
}
</style>