<template>
  <div>
    <el-form :model="form" label-suffix=":">
      <el-form-item label="" :label-width="formLabelWidth" >
        <span>{{ form.username }}</span>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <span>{{ form.sender }}</span>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <span>{{ form.demandName }}</span>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <span>{{ form.content }}</span>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" >
        <span>{{ form.address }}</span>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
