    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="submitForm"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "DonDialog",
  data() {
    return {
      formLabelWidth: "120px"
    }
  },
  props: {
    form: {
      type: Object,
      default: {
        name: "()",
        supplies: "KN951000",
        contact: '',
        phone: '15294457513',
        address: '',
        addressDetaile: ''
      }
    }
  },
  methods: {
    submitForm() {
      this.$emit("sureDialog")
      this.form = {}
    },
  }
}
</script>

<style scoped>
.dialog-footer {
  text-align: right;
}
</style>