<template>
  <div>
    <el-form :model="form" :rules="rules" label-suffix=":" ref="demForm">
      <el-form-item label="" :label-width="formLabelWidth" prop="username">
        <el-input v-model="form.username" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="content">
        <el-input v-model="form.content" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="image">
        <el-input v-model="form.image" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="address">
        <el-input v-model="form.address" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth" prop="courier">
        <el-input v-model="form.courier" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="success" @click="sureDemand('demForm')"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "InnerDialog",
  data() {
    return {
      formLabelWidth: "95px",
      form: {},
      rules: {
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 18, message: ' 3  18 ', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '', trigger: 'blur'}
        ],
        image: [
          {required: true, message: '', trigger: 'blur'}
        ],
        address: [
          {required: true, message: '', trigger: 'blur'}
        ],
        courier: [
          {required: true, message: '', trigger: 'blur'}
        ],
      }
    }
  },
  methods: {
    sureDemand(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$emit("sureDemand", this.form)
        } else {
          return false;
        }
      });
    }
  }
}
</script>

